* {
    box-sizing: border-box;
    padding: 0;
}
body {
    margin: 0;
    padding: 10px;
    background-color: #eff3f7;
    height: 100%;
}
a {
    text-decoration: none;
    color: inherit;
}
.nav {
    position: fixed;
    top: 24px;
    left: 24px;
    z-index: 999;
    padding: 10px 20px;
    box-shadow: 0 12px 28px rgba(50,50,93,0.4);
    border-radius: 4px;
    background-color: #fff;
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
}
.nav a {
    text-decoration: none;
    font-size: 12px;
    text-align: center;
    font-weight: 600;
    color: #193386;
}

.movies .movie {
    background-color: #fff;
    font-weight: 300;
    color: #adaeb9;
    padding: 20px;
    border-radius: 5px;
    box-shadow: 0 13px 27px -5px rgba(50,50,93,0.3);
    margin-bottom: 70px;
}
/* 플렉스 2단 레이아웃 */
.movies {
    display: -webkit-flex;
    display: flex;
    -webkit-flex-flow: row wrap;
            flex-flow: row wrap;
}
.movies .movie {
    width: 48%;
    margin: 24px 1%;
}
.movie .movie_title {
    color: #2c2c2c;
    font-size: 24px;
    margin-bottom: 4px;
    font-weight: 400;
}

.movie .movie_year {
    font-size: 18px;
    font-weight: 500;
    color: rgb(31, 25, 25);
}

/* li 한줄로 레이아웃 */
.movie .movie_genre {
    list-style: none;
    display: inline-block;
    margin-right: 16px;
    padding: 0;
}

@media screen and (max-width:425px){
    /* 플렉스 1단 레이아웃 */
    .movies {
        display: -webkit-flex;
        display: flex;
        -webkit-flex-flow: row wrap;
                flex-flow: row wrap;
    }
    .movies .movie {
        width: 100%;
        margin: 24px 1%;
        margin: 0;
    }
}
/* loading 시 글자 애니메이션 */
.loader {
    font-size: 2rem;
    font-weight: 300;
    color: rgb(45, 101, 134);
    text-align: center;
    font-family: Oswald,sans-serif;
}
.loader_text {
    -webkit-animation-name: loader;
            animation-name: loader;
    -webkit-animation-duration: 1s;
            animation-duration: 1s;
    -webkit-animation-iteration-count: infinite;
            animation-iteration-count: infinite;
}
@-webkit-keyframes loader {
    from {
        opacity: 0;
    }
    to {
        opacity: 1;
    }
}
@keyframes loader {
    from {
        opacity: 0;
    }
    to {
        opacity: 1;
    }
}
